<script>
export default {
  name: 'RenderLine',
  props: {
    text: String,
    level: Number,
  },
  render: function (createElement) {
    console.log(this.text.split('\n'))
    if (this.text.startsWith('######') === true) {
      return createElement(
        'h6',
        this.text.substring(6, this.text.length)
      )
    } else if (this.text.startsWith('#####') === true) {
      return createElement(
        'h5',
        this.text.substring(5, this.text.length)
      )
    } else if (this.text.startsWith('####') === true) {
      return createElement(
        'h4',
        this.text.substring(4, this.text.length)
      )
    } else if (this.text.startsWith('###') === true) {
      return createElement(
        'h3',
        this.text.substring(3, this.text.length)
      )
    } else if (this.text.startsWith('##') === true) {
      return createElement(
        'h2',
        this.text.substring(2, this.text.length)
      )
    } else if (this.text.startsWith('#') === true) {
      return createElement(
        'h1',
        this.text.substring(1, this.text.length)
      )
    } else if (this.text.startsWith('```') === true) {
      return createElement(
        'pre',
        this.text.substring(3, this.text.length - 3)
      )
    } else if (this.text.startsWith('![alt text](') === true) {
      const el = createElement(
        'img',
        {
          attrs: {
            src: 'https://github.com/hpennington/darktorch/raw/master/abbey.jpg'
          }
        }
      )
      //el.elm.src = 'https://github.com/hpennington/darktorch/raw/master/abbey.jpg'
      console.log(el)
      return el
    } else {
      return createElement(
        'p',
        this.$slots.default
      )
    }
  },
}
</script>

<style scoped>
pre {
  background-color: #fafafa;
  margin: 1em;
  overflow-x: scroll;
}
img {
  width: 100%;
  height: 100%;
}
</style>
